.themes {
	text-align: center;
	min-height: 1000px;
	@include r(1200) {
		min-height: 650px; }
	@include r(900) {
		min-height: 600px; } }


.themes__title {
	font-size: 80px;
	color: $white;
	font-weight: 700;

	@include r(1200) {
		margin-bottom: 320px; }
	@include r(900) {
		font-size: 40px; }
	@include r(400) {
		font-size: 30px; } }

.themes-wrapper {
	background: linear-gradient(0deg, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url(/img/header-bg-light.jpg);
	background-size: cover;
	width: 100%;
	padding: 100px 100px 0px 100px;
	position: relative;
	z-index: 1;

	@include r(1200) {
		padding: 20px 20px 55px 20px; }
	@include r(900) {
		padding: 55px; } }

.themes-wrapper::after {
	content: '';
	display: block;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
	background-image: url(/img/Hmm-on-dark.gif);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 25%;

	@include r(1200) {
		background-position: center 130px;
		background-size: 300px; }
	@include r(900) {
		background-position: center 110px;
		background-size: 300px; } }

.themes__list {
	position: relative;
	z-index: 3;
	display: inline-block;
	width: 100%;
	min-height: inherit;
	transform: rotate(0deg);
	@include r(1200) {
		max-width: 500px; } }

.themes__item {
	width: calc(100% / 3);
	transform-origin: 150% 26px;
	transform: rotate(18deg);
	position: absolute;
	top: 45%;
	font-size: 24px;
	font-weight: 700;
	color: #FFCC4D;

	@include r(1200) {
		margin-bottom: 35px; } }

.themes .themes__item {
	@include r(1200) {
		position: static;
		transform: none;
		transform-origin: unset;
		width: 100%; } }

.themes .themes__item .themes__text {
	@include r(1200) {
		position: static;
		transform: none;
		transform-origin: inherit;
		width: 100%;
		text-align: left;
		font-size: 20px; } }

@for $i from 1 through 10 {
	.themes__item:nth-child(#{$i}) {
		transform: rotate(((($i - 1) * 360deg) / 10) + 18); }
	.themes__item:nth-child(#{$i}) .themes__text {
		transform: rotate(-1 * (((($i - 1) * 360deg) / 10) + 18)); } }
