body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 940px;
  width: 100%;
  min-width: 300px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  box-sizing: border-box;
  font-weight: bold;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0;
}

.header {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.header-bg {
  z-index: -100;
  position: absolute;
  width: 100%;
  height: 100%;
}

.header-bg-img {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../../img/header-bg-light.jpg);
  background-size: 100% 100%;
}

