.header-content {
  width: 100%;
  height: 100%;
  padding: 0 50px 50px 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.header-menu {
  margin-bottom: 20px;
}

.header-menu-list {
  display: flex;
}

.header-menu-list > li > a {
  margin: 0px 20px 20px 20px;
  font-size: 16px;
  color: white;
  text-decoration: none;
  border-bottom: 2px solid #fff4;
}

.header-menu-list > li > a {
  display: inline-block;
  animation: moveInMenuAnimation 1s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
}

.header-menu-list > li:nth-of-type(1) > a {
  animation-delay: 0.1s;
}

.header-menu-list > li:nth-of-type(2) > a {
  animation-delay: 0.2s;
}

.header-menu-list > li:nth-of-type(3) > a {
  animation-delay: 0.3s;
}

.header-menu-list > li:nth-of-type(4) > a {
  animation-delay: 0.4s;
}

.header-menu-list > li:nth-of-type(5) > a {
  animation-delay: 0.5s;
}

.header-menu-list > li:nth-of-type(6) > a {
  animation-delay: 0.6s;
}

.header-menu-list > li:nth-of-type(7) > a {
  animation-delay: 0.7s;
}

.header-menu-list > li:nth-of-type(8) > a {
  animation-delay: 0.8s;
}

.header-menu-list > li > a:hover {
  color: red;
  border-bottom: 2px solid #f006;
}

.header-logo {
  margin: 0 auto;
  width: 353px;
  height: 70px;
  margin-bottom: 5px;
  animation: logoAnimation 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.header-logo > img {
  width: 100%;
}

.header-center-text {
  text-align: center;
  font-size: 20px;
  line-height: 27px;
  color: white;
  width: 470px;
}

.header-text-wrapper {
  overflow: hidden;
}

.header-center-text > div > p {
  animation: moveInAnimation 1s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
}

.header-center-text > div:nth-of-type(1) > p {
  animation-delay: 2.5s;
}

.header-center-text > div:nth-of-type(2) > p {
  animation-delay: 2.6s;
}

.header-center-text > div:nth-of-type(3) > p {
  animation-delay: 2.7s;
}

.header-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header-bottom-left,
.header-bottom-right {
  width: 410px;
}

.header-bottom-left > p,
.header-bottom-right > p {
  margin: 0;
  color: #fff6;
  font-size: 16px;
  animation: moveInButtomAnimation 1s 3.2s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
}

.header-bottom-right {
  text-align: right;
}

.header-bottom-left > p > a,
.header-bottom-right > p > a {
  color: #fff6;
  text-decoration: none;
  border-bottom: 2px solid #fff4;
}

.header-bottom-left > p > a:hover,
.header-bottom-right > p > a:hover {
  border-bottom: 2px solid #f006;
  color: red;
  opacity: 1;
}

.header-bottom-left > h3,
.header-bottom-right > h3 {
  line-height: 1.1;
  color: white;
  margin: 0;
  font-size: 35px;
  animation: moveInButtomAnimation 1s 3.3s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
}

.header-bottom-button {
  font-weight: bold;
  font-size: 16px;
  color: white;
  width: 144px;
  height: 47px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%), linear-gradient(94.81deg, #b2f000 0%, #58c207 47.92%, #00d287 100%);
  background-blend-mode: overlay, normal;
  border: none;
  border-radius: 7px;
  transition: all 250ms ease-in-out;
  animation: moveInButtonAnimation 1s 3s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
}

.header-bottom-button:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 13px 15px -10px #50b70077;
}

@keyframes logoAimation {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}

@keyframes moveInAnimation {
  from {
    transform: translateY(27px);
  }
  to {
    transform: translateY(0px);
  }
}

@keyframes moveInMenuAnimation {
  from {
    opacity: 0;
    transform: translateY(57px);
  }
  to {
    transform: translateY(0px);
  }
}

@keyframes moveInButtonAnimation {
  from {
    opacity: 0;
    transform: translateY(57px);
  }
  to {
    transform: translateY(0px);
  }
}

@keyframes moveInButtomAnimation {
  from {
    opacity: 0;
    transform: translateY(57px);
  }
  to {
    transform: translateY(0px);
  }
}

