.card {
  min-height: 610px;
  max-width: 450px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -o-border-radius: 20px;
  color: #fff;
  font-weight: 900;
  padding: 40px;
  font-size: 24px;
  letter-spacing: 0.01em;
