.footer-wrapper {
  background-color: $black; }

.footer__text-wrapper {
    padding: 10px; }

.footer__text {
  color: $white;
  font-size: 12px;
  font-weight: 700;
  text-align: center;

  @include r(490) {
    font-size: 10px; } }

.footer__love {
  width: 20%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-bottom: 55px;
  margin: 0 auto;

  @include r(710) {
    width: 40%; } }

.footer__heart-img {
  display: inline-block;
  font-size: 28px;
  color: $red;
  margin-bottom: 15px;
  @include r(400) {
    margin-bottom: 5px;
    padding: 5px; } }

.footer__input-conf-img {
  width: 50px;
  padding-bottom: 3px; }

.footer__logo-img {
  width: 50px; }


